import React from "react";
import * as yup from 'yup'
import { setLocale } from 'yup';
import {compose} from "redux";
import {connect} from "react-redux";

class YupInputs extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            name: '',
            age: '',
            website: '',
            email: '',
            phone: '',

            isValid: false,
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)

    }

    handleChange(e) {
        let value = e.target.value
        let name = e.target.name

        this.setState({
            [name]: value
        })
    }
    validate(shape) {

        setLocale({
            string: {
                name: ' '
            },
            number: {
                min: `  - 18 `,
                integer: `   `,
                positive: `   `,
            },
        });

        let schema = yup.object().shape({
            name: yup.string().required(' '),
            age: yup.number(' ').positive().integer().min(18),
            website: yup.string().url('  website,  ?'),
            email: yup.string().required(' email').email('  email,  ?'),
        });

        //   catch,   then         
        schema
            .validate(shape)
            .then(function (valid) {

                console.log(valid);
            })
            .catch(function (err) {

                console.log(err.errors);
            })
            //shape -  
    }

    handleSubmit() {
        this.validate({
            name: this.state.name,
            age: this.state.age === '' ? 0 : this.state.age,
            website: this.state.website,
            email: this.state.email,
        })
    }

    componentDidMount() {

    }

    render() {
        return(
            <div>
                <div><input type="text" value={this.state.name} placeholder={''} name='name' onChange={this.handleChange}/></div>
                <div><input type="number" value={this.state.age} placeholder={''} name='age' onChange={this.handleChange}/></div>
                <div><input type="url" value={this.state.website} placeholder={''} name='website' onChange={this.handleChange}/></div>
                <div><input type="email" value={this.state.email} placeholder={''} name='email' onChange={this.handleChange}/></div>

                <button onClick={this.handleSubmit}>submit</button>

            </div>
        )
    }
}



let mapStateToProps = (state) => {
    return {
        data: state.test.data
    }
}

export default compose(
    connect(mapStateToProps, {})
)(YupInputs)
